<h1 id="android-gps" tabindex="-1"><a class="header-anchor" href="#android-gps" aria-hidden="true">#</a> Android GPS</h1>
<p>So, you need to figure out where in the world the user is? Then meet your new best friend: the GPS receiver. Let's take a look at how to use it in Android, with and without Google Play Services.</p>
